<template>
  <div>
    <NavbarShop />
    <div class="container mt-5">
      <div id="frame" class="mx-auto">
        <nav class="navbar navbar-expand navbar-light custom-bg-primary header-bar">
          <router-link
            to="/ShopManager/FoodType"
            class="nav-link back-link navbar-brand mb-2"
            ><button type="button" class="btn btn-danger">
              <i class="bi bi-backspace"></i>
            </button>
          </router-link>
          <p class="type mt-2">{{ Type }}</p>
        </nav>

        <div class="" v-for="foodByType in FoodsByType()" :key="foodByType._id">
          <div class="list my-2 row mx-auto">
            <img
              class="col-5 p-1"
              v-bind:src="`/FoodImage/${foodByType.imgName}`"
              alt="image not avail"
            />

            <span class="col-4 p-0"
              ><p class="prop">Name: {{ foodByType.name }}</p>
              <p class="prop" v-bind:class="{ crossPrice: foodByType.promotion }">
                Price(Baht): {{ foodByType.price }}
              </p>
              <div v-show="foodByType.promotion">
                <p class="prop">New Price: {{ foodByType.promotionPrice }}</p>
                <p class="prop">Detail: {{ foodByType.promotionDetail }}</p>
              </div>
            </span>
            <span class="col-3 d-flex flex-column justify-content-around px-1 px-md-4">
              <!-- <button type="button" class="btn btn-info custom-btn w-75 mx-auto">
                Edit
              </button> -->
              <router-link
                :to="{ name: 'EditFood', params: { id: foodByType._id } }"
                class="btn btn-info custom-btn w-100 mx-auto"
                >Edit</router-link
              >
              <button
                @click.prevent="deleteFood(foodByType._id)"
                type="button"
                class="btn btn-danger custom-btn w-100 mx-auto"
              >
                Delete
              </button></span
            >
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import NavbarShop from "@/components/NavbarShop.vue";
export default {
  name: "FoodByType",
  data() {
    return {
      Foods: [],
      Type: this.$route.params.type,
    };
  },
  created() {
    let apiURL = "http://localhost:4000/api";
    axios
      .get(apiURL)
      .then((res) => {
        this.Foods = res.data;
      })
      .catch((error) => {
        console.log(error);
      });
  },
  methods: {
    // Filter to get only selected type
    FoodsByType: function () {
      let FoodsByType = this.Foods.filter(
        (food) => food.type === this.$route.params.type
      );
      return FoodsByType.reverse();
    },

    deleteFood(id) {
      let apiURL = `http://localhost:4000/api/delete-food/${id}`;
      let indexOfArrayItem = this.Foods.findIndex((i) => i._id === id);

      if (window.confirm("Do you really want to delete?")) {
        axios
          .delete(apiURL)
          .then(() => {
            this.Foods.splice(indexOfArrayItem, 1);
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },
  },
  components: {
    NavbarShop,
  },
};
</script>
<style scoped>
@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.4.1/font/bootstrap-icons.css");
#frame {
  background: 1px solid black;
}
.header-bar {
  height: 45px;
  background-color: yellow;
}
.back-link {
  height: 100%;
  padding: 0;
  display: inline;
}

.type {
  display: inline;
  font-weight: 500;
  font-size: 25px;
}
#frame {
  border: 1px solid black;
  max-width: 950px;
}
.list {
  width: 100%;
  height: 20vh;
  border: 1px solid black;
}
span {
  height: inherit;
}
.prop {
  margin: 0;
  font-size: calc(0.65em + 0.5vw);
  overflow-wrap: break-word;
}
.custom-btn {
  font-size: calc(0.7em + 0.5vw);
}
.crossPrice {
  text-decoration: line-through;
  text-decoration-color: red;
}
img {
  height: inherit;
  object-fit: cover;
}
</style>
