<template>
  <div>
    <NavbarShop />
    <div class="mt-3 container">
      <div class="row justify-content-center">
        <div class="col-md-6">
          <h1>Edit Menu</h1>
          <form class="" @submit.prevent="handleUpdateFood" enctype="multipart/form-data">
            <div class="form-group">
              <label for="name">Name</label>
              <input
                type="text"
                class="form-control"
                id="name"
                v-model="food.name"
                required
              />
            </div>
            <!-- <div class="form-group">
              <label for="type">Type</label>
              <input type="text" class="form-control" id="type" required />
            </div> -->
            <div class="form-group">
              <label for="type">Type of Food</label>
              <select
                id="type"
                class="form-control"
                v-model="food.type"
                placeholder="Select Type of Food"
                required
              >
                <option>Appetizer</option>
                <option>Main Course</option>
                <option>Dessert</option>
                <option>Drinks</option>
                <option>Combo Set</option>
              </select>
            </div>
            <div class="form-group">
              <label for="price">Price in Baht</label>
              <input
                type="number"
                class="form-control"
                id="price"
                v-model="food.price"
                required
              />
            </div>
            <div class="form-group">
              <label for="file">Upload Image</label>
              <input
                type="file"
                ref="file"
                name="file"
                id="file"
                class="form-control-file"
                v-on:change="selectfile"
              />
            </div>

            <div class="form-check my-2">
              <input
                class="form-check-input"
                type="checkbox"
                value="true"
                id="Promotion?"
                v-model="food.promotion"
              />
              <label class="form-check-label" for="Promotion?"> Promotion? </label>
            </div>

            <transition name="slide">
              <div v-show="food.promotion" id="promo">
                <div class="form-group">
                  <label for="promotion-price">Promotion Price in Baht</label>
                  <input
                    type="number"
                    class="form-control"
                    id="promotion-price"
                    v-model="food.promotionPrice"
                  />
                </div>
                <div class="form-group">
                  <label for="promo-detail">Promotion Details</label>
                  <textarea
                    class="form-control"
                    aria-label="With textarea"
                    id="promo-detail"
                    v-model="food.promotionDetail"
                  ></textarea>
                </div>
              </div>
            </transition>

            <div class="form-group">
              <button class="btn btn-primary btn-block">Update</button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import NavbarShop from "@/components/NavbarShop.vue";
import axios from "axios";
export default {
  name: "EditFood",
  components: {
    NavbarShop,
  },
  data() {
    return {
      food: {},
      file: "",
    };
  },
  created() {
    let apiURL = `http://localhost:4000/api/getAfood/${this.$route.params.id}`;
    axios.get(apiURL).then((res) => {
      this.food = res.data;
    });
  },
  methods: {
    selectfile() {
      this.file = this.$refs.file.files[0];
      console.log(this.file);
    },
    handleUpdateFood() {
      let apiURL = `http://localhost:4000/api/update-food/${this.$route.params.id}`;

      if (this.file) {
        let apiUpload = "http://localhost:4000/api/upload";
        const formData = new FormData();
        formData.append("file", this.file, this.file.name);
        //upload image
        axios
          .post(apiUpload, formData)
          .then(() => {
            this.file = "";
            console.log(this.$refs.file.files[0].name);
            this.food.imgName = this.$refs.file.files[0].name;
          })
          .then(() => {
            //upload data
            axios.put(apiURL, this.food).then((res) => {
              console.log(res);
            });
          })
          .then(() => {
            this.$router.push(`/ShopManager/foodbytype/${this.food.type}`);
          })
          .catch((error) => {
            console.log(error);
          });
      } else {
        axios
          .put(apiURL, this.food)
          .then((res) => {
            console.log(res);
          })
          .then(() => {
            this.$router.push(`/ShopManager/foodbytype/${this.food.type}`);
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },
  },
};
</script>

<style scoped>
.slide-enter-active {
  -moz-transition-duration: 0.3s;
  -webkit-transition-duration: 0.3s;
  -o-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -moz-transition-timing-function: ease-in;
  -webkit-transition-timing-function: ease-in;
  -o-transition-timing-function: ease-in;
  transition-timing-function: ease-in;
}

.slide-leave-active {
  -moz-transition-duration: 0.3s;
  -webkit-transition-duration: 0.3s;
  -o-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -moz-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
  -webkit-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
  -o-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
  transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
}

.slide-enter-to,
.slide-leave {
  max-height: 100px;
  overflow: hidden;
}

.slide-enter,
.slide-leave-to {
  overflow: hidden;
  max-height: 0;
}
</style>
